@import "sass/index";

.wrapper {
  padding: spacing(4.25);
}
.imageSkeleton {
  height: 260px;
  width: 100%;
  margin: spacing(4) 0;
  background: rgba(black, 0.5);
  border-radius: 21px;
}
.title {
  & p {
    font-family: "Gilroy";
    font-weight: bold;
    font-size: 22px;
    color: white;
    letter-spacing: 0;
  }
}
.questionBlock {
  &:not(:first-child) {
    padding-top: spacing(8);
  }
}
.text {
  & > span {
    font-family: "Gilroy";
    font-size: 16px;
    color: white;
    letter-spacing: 0;
    line-height: 21px;
  }
}
.question {
  padding-top: 20px;
  padding-bottom: 20px;
  & > span {
    font-weight: 600;
    font-size: 18px;
    color: #b78fff;
    line-height: 23px;
  }
}
.accordion {
  & > div:not(:last-child) {
    margin-bottom: 20px;
  }
}
